*{
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1,h2,h3{
  text-align: center;
  margin: 0 auto;
}

main{
  margin: 0 auto;
  max-width: 1500px;

  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  justify-content: center;
}

#demo-section{
  width: 100%;
  position: relative;

  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: row nowrap;
  overflow: hidden;
}

#intro-section{
  margin: 0 auto;
  padding: 24px 0px;

  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 0.5fr 2fr 2fr;
  gap: 16px;
}

#intro-section p{
  padding: 8px;
  text-align: justify;
}

#title{
  grid-column: 1 / 1;
  grid-row: 1 / 1;
}

#intro-paragraph{
  grid-column: 1 / 1;
  grid-row: 2 / span 2;
}

#showcase-image{
  width: 100%;
  height: 100%;

  grid-column: 2 / 2;
  grid-row: 1 / span 3;
  border: #1FFF19 5px solid;
}

.row-wrapper{
  padding: 10px;

  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
}

.row-row{
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  justify-content: space-evenly;
  gap: 24px;
}

.row-column{
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
}

.row-column *{
  margin: 10px 0;
}

.row-column input[type="checkbox"]{
  width: auto;
}

.row-column input{
  width: 250px;
}

#options-container{
  position: absolute;
  height: 100%;

  top: 0;
  right: 0;
  background: #FFFFFF;
  background: -webkit-linear-gradient(top left, #FFFFFF, #7FFF19);
  background: -moz-linear-gradient(top left, #FFFFFF, #7FFF19);
  background: linear-gradient(to bottom right, #FFFFFF, #7FFF19);
  border: #1FFF19 5px solid;
}

#generate-button{
  width: 150px;
}

#card-container{
  padding: 8px;

  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
}

.card{
  max-width: 300px;
  padding: 8px;
  margin-top: 8px;

  border: #1FFF19 5px solid;
}

.card p{
  text-align: center;
}

.style-list li{
  margin: 4px 0;
}